/* ======= COLUMNS ======= */

.cols {
    display: block;
    margin-bottom: 50px;
}

.cols>div {
    display: block;
    margin-bottom: 50px; 
    color: $darkgrey;
}

.cols>div>img {
    width: 100%;
}

.cols>div>h1,
.cols>div>p {
    max-width: 500px;
}

.cols>div>h1 {
    font-family: $header;
    font-size: 1.3rem;
    font-weight: normal;
    line-height: 1.6rem;
    color: $black;
    padding: 1.8rem 0 .2rem;
    margin: 0;
    text-transform: initial;
}

.cols>div>h2 {
    font-family: $header;
    font-size: 1.1rem;
    font-weight: 500;
    line-height: 1.4rem;
    color: $midblue;
    padding: 1.6rem 0 .8rem;
    margin: 0;
}

.cols>div>p {
    width: 95%;
    font-size: .8rem;
    // Calculate line-height based on viewport width
    line-height: calc(1.2rem + .3 * ((100vw - 22.5rem ) / 40));
}

.cols>div>p:first-child {
    padding-top: 0;
}

.cols>div>p:last-child {
    padding-bottom: 2rem;
}

// Partial Width
@media screen and (min-width: $break1) {
    .cols>div:after {
        content: '';
        display: block;
        clear: both;
    }
    .cols>div>img {
        width: 50%;
        margin: 0 0 5% 5%;
        float: right;
    }
    .cols>div>h1 {
        padding: 0 0 .2rem;
    }
}

// Full Width
@media screen and (min-width: $break2) {
    .cols {
        display: flex;
        justify-content: space-between;
    }
    .cols>div {
        margin: 0;
    }
    .cols.halves>div {
        flex-basis: 48%;
        min-width: 320px;
    }
    .cols.thirds>div {
        flex-basis: 31%;
        width: 31%;
    }
    .cols>div>img {
        width: 100%;
        margin: 0;
        float: none;
    }
    .cols>div>h1 {
        padding: 1.8rem 0 .2rem;
    }
}

// Static line-height at full width
@media screen and (min-width: $break3){
    .cols>div>p {
        line-height: 1.5rem;
    }
}

// Buttons

.cols>div>a.button {
    margin: 1.2rem 0;
    padding: calc(1% + 1em) 0;
    width: 100%;
    background: $white;
    border: 1px solid $grey;
    font-size: .7rem;
    font-weight: 700;
    letter-spacing: .1rem;
    text-transform: uppercase;
    text-align: center;
    color: $blue;
    transition: all .2s;
}

.cols>div>a.button:hover,
.cols>div>a.button:focus {
    background: $blue;
    border: 1px solid $blue;
    color: $white;
    transition: all .1s;
}

@media screen and (min-width: $break1) {
    .cols>div>a.button {
        width: 45%;
    }
}

@media screen and (min-width: $break2) {
    .cols>div>a.button {
        width: 100%;
    }
}